import React, { useState } from 'react';
import './mainMenu.scss';
import AppManager from '../managers/appManager';
import { APP_STATE } from '../consts/states';
import NavBar from './UI/navBar';
import { NAV_ENTRY } from '../models';
import SlotMachineModal from './UI/SlotMachineModal';

interface MainMenuProps {
  appManager: AppManager; 
}

const MainMenuComponent: React.FC<MainMenuProps> = ({appManager}) => { 
  const [isSlotMachineOpen, setIsSlotMachineOpen] = useState(false);

  const handleQuickStart = () => {
    appManager.initiateQuickStartSetup();
    appManager.state = APP_STATE.SESSION_OVERVIEW;
  }

  const openSlotMachine = () => {
    setIsSlotMachineOpen(true);
  }

  const closeSlotMachine = () => {
    setIsSlotMachineOpen(false);
  }

  React.useEffect(() => {
    openSlotMachine();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <div className="main-menu-container">
      <div className="main-menu-content">
        
        <div className="main-menu-options">
         <NavBar buttonList={[NAV_ENTRY.PLAYER_RECRUITMENT, NAV_ENTRY.CHARACTER_ASSIGNMENT, NAV_ENTRY.CAMPAIGN]} highlight={NAV_ENTRY.PLAYER_RECRUITMENT}/>
        </div>
        <div className="game-start-container">
          <div className="button-container">
            <div className={`quickstart-button nav-button`} onClick={handleQuickStart}>Quick Start</div>
          </div>
          <div className="button-container">
            <div className={`minigame-button nav-button`} onClick={openSlotMachine}>Regal Richez</div>
          </div>
        </div>
      </div>

      {/* Slot Machine Modal */}
      <SlotMachineModal 
        isOpen={isSlotMachineOpen} 
        onClose={closeSlotMachine} 
      />
    </div>
  );
};

export default MainMenuComponent;
