  exposeCurrentlocationTag(tag: Tag) {
    // This is used to expose the current location tag to the players.
    this.collectedLocationTags.push(tag); // add to the collected location tags
    this.addCampaignScore(COST.CAMPAIGN_SCORE.LOCATION_DESCRIBED);
    if (this.currentEncounter) {
      this.appManager.getUiManager().setEventImageOpacity(this.collectedLocationTags.length / this.currentEncounter?.location.tags.length);
    }

  }

  // Check against magic items, backstories, etc and change the location based on that. This will then set the style
  getFormattedSource = (tag: Tag, source: FlavorTagSource) => {
    let formattedSource = source;
    if (this.isMagicItemRumor(tag.name) || tag.type === TAG_TYPE.GEAR) {
      formattedSource = FlavorTagSource.MAGIC_ITEM;
    } else if (this.isBackstoryTag(tag.name)) {
      formattedSource = FlavorTagSource.BACKSTORY;
    }
    else if (this.isLocationRumor(tag.name) || this.isEncounterLocationTag(tag.name)) {
      formattedSource = FlavorTagSource.LOCATION;
    } else if (this.isCurrentEncounterTag(tag.name)) {
      //  debugger
      formattedSource = FlavorTagSource.FORESHADOWING;
    }
    else if (this.isUpcomingEncounterTag(tag.name)) {
      //  debugger
      formattedSource = FlavorTagSource.FORESHADOWING;
    }


    return formattedSource;
  }

  isCurrentEncounterTag(tagName: string) {
    if (!this.currentEncounter) {
      return false;
    }
    return this.expectedTagsForBranches[this.currentEncounter.name].some(tag => tag.name === tagName);
  }


  isUpcomingEncounterTag(tagName: string) {
    if (!this.currentEncounter) {
      return false;
    }
    const upcomingBranches = this.currentEncounter.branches || [];
    return upcomingBranches.some(branch => branch.tags.some(tag => tag.name === tagName));
  }


  advanceCampaignStory() {
    this.partyManager().updateAllAttention(2);
    this.setCampaignProgress(this.campaignProgress + 1);
    this.addCampaignScore(25);
    this.emit('campaignProgressUpdated', this.campaignProgress);
  }


  getCampaignTooltipForTag(flavorTag: EncounterFlavorTag): string | undefined {
    const campaign = CAMPAIGNS[this.currentCampaign || 0];

    if (campaign.tooltipTags && campaign.tooltipTags[this.currentEncounter?.name || '']?.[flavorTag.tag.name]) {
      const tooltip = campaign.tooltipTags[this.currentEncounter?.name || '']?.[flavorTag.tag.name];
      if (tooltip) {
        return tooltip + this.appManager.getChatManager().getThemePostFix(flavorTag);
      }
    }
    return undefined;
  }

  //const encounter = campaign.processedEncounters?.flat().find(encounter => encounter.tags.includes(tag));

  couldAssignRandomTagToBranch(encounter: Encounter) {

    // How many authored tags are there 
    const nbTagsExpected = encounter.tags.length;
    // Total number of random tags allowed. 
    const totalNbRandomTagsAllowed = 4 - nbTagsExpected;

    // These are tags assigned to the branch already, but we dont know if they are authored or not
    const nbTagsAlreadyAssignedToBranch = this.expectedTagsForBranches[encounter.name].length;

    // find out how many authored tags are among the tags already assigned to the branch
    const nbAuthoredTagsInExpectedTags = encounter.tags.filter(t => this.expectedTagsForBranches[encounter.name].includes(t)).length;
    const nbRandomTagsAlreadyAssigned = nbTagsAlreadyAssignedToBranch - nbAuthoredTagsInExpectedTags;

    if (nbRandomTagsAlreadyAssigned >= totalNbRandomTagsAllowed) {
      return false;
    }
    return true;
  }


  getUpcomingEncounterTags() {
    if (!this.currentEncounter) {
      return [];
    }
    const upcomingBranches = this.currentEncounter.branches || [];
    let upcomingTags: Tag[] = [];
    upcomingBranches.forEach((branch) => {
      upcomingTags = [...upcomingTags, ...branch.tags.filter(tag => !this.expectedTagsForBranches[branch.name].includes(tag))];
    });
    return upcomingTags;
  }


  resolveTownTag(flavorTag: EncounterFlavorTag) {
    this.updateFlavorTagThemesTally([flavorTag]);

    // adjust player happiness based on this tag
    this.playerManager.exposeEncounterTag(flavorTag.tag);

    this.exposeTagForEncounter(flavorTag);

  }

  couldAssignRandomTag() {
    if (!this.currentEncounter) {
      return 0;
    }
    const upcomingBranches = this.currentEncounter.branches || [];
    let couldAssignRandomTagToAnyBranch = false;
    upcomingBranches.forEach((branch) => {
      if (this.couldAssignRandomTagToBranch(branch)) {
        couldAssignRandomTagToAnyBranch = true;
      }
    });
    return couldAssignRandomTagToAnyBranch;
  }

  getExpectedGear() {
    return this.getRumorTags().filter(tag => tag.type === TAG_TYPE.GEAR);
  }

  getActiveThemes() {
    // sum of all keys in themeCounts with a number greater than 0
    return Object.values(this.themeCounts).reduce((sum, count) => sum + (count > 0 ? 1 : 0), 0);

  }

