  getTheme(): FlavorTagTheme {

    // 1 in 5 chance of having a theme
    if (Math.floor(Math.random() * 5) !== 0) {
      return FlavorTagTheme.NONE;
    }

    const themeRoll = Math.floor(Math.random() * 4);

    if (themeRoll === 0) {
      return FlavorTagTheme.COMEDIC;
    } else if (themeRoll === 1) {
      return FlavorTagTheme.DRAMATIC;
    } else if (themeRoll === 2) {
      return FlavorTagTheme.SERIOUS;
    }
    // } else if (themeRoll === 3) {
    //   return FlavorTagTheme.GRIM;
    // }
    // else if (themeRoll === 4) {
    //  return FlavorTagTheme.HEROIC;
    // } else if (themeRoll === 5) {
    //   return FlavorTagTheme.ROMANTIC;
    // } else if (themeRoll === 6) {
    //   return FlavorTagTheme.FANTASY;
    // } else if (themeRoll === 7) {
    //   return FlavorTagTheme.HORROR;
    // }

    return FlavorTagTheme.NONE;

  }

