import React, { useState, useEffect } from "react";

import {
  useAppManager,
  useCampaignManager,
  useCharacterManager,
  usePlayerManager,
  useUiManager,
} from "../managerContexts";
import "./gameRoom.scss";
import partyManager from "../../managers/partyManager";
import {
  Encounter,
  TRAVEL_LENGTH,
  TravelOption,
  Tag,
  ModalConfig,
  TravelResultConfig,
  FlavorTagSource,
  FlavorTagTheme,
  EncounterFlavorTag,
  TRAVEL_OPTION,
  Character,
  Player,
} from "../../models";
import "../UI/encounterStyle.scss";
import TravelOptionDisplay from "../UI/travelOption";
import { TAG } from "../../consts/tags";
import EffectDisplay from "../UI/effectDisplay";
import { time } from "console";
import FlavorTagPill from "../UI/FlavorTagPill";
import TooltipExamples from "../UI/TooltipExamples";
import TooltipWrapper from "../UI/TooltipWrapper";
interface GameEncounterTravelProps {
  encounter: Encounter;
}

interface JourneyLength {
  rating: "EPIC" | "GREAT" | "GOOD" | "SHORT" | "FUTILE";
  score: number;
}

const GameEncounterTravel: React.FC<GameEncounterTravelProps> = ({
  encounter,
}) => {
  const [travelLength, setTravelLength] = useState<TRAVEL_LENGTH>(
    TRAVEL_LENGTH.MEDIUM
  );
  const [availableOptions, setAvailableOptions] = useState<TravelOption[]>([]);
  const [usedTravelOptionIds, setUsedTravelOptionIds] = useState<string[]>([]);
  
  // Add reward position state
  const [bigRewardSection, setBigRewardSection] = useState<string>("");
  const [smallRewardSections, setSmallRewardSections] = useState<string[]>([]);

  const appManager = useAppManager();
  const campaignManager = useCampaignManager();
  const characterManager = useCharacterManager();
  const playerManager = appManager.getPlayerManager();
  const uiManager = useUiManager();
  const partyManager = appManager.getPartyManager();

  const [travelOptions, setTravelOptions] = useState<TravelOption[][]>(
    campaignManager.getTravelOptions()
  );
  const [selectedOption, setSelectedOption] = useState<TravelOption | null>(
    null
  );
  const [selectedTags, setSelectedTags] = useState<Tag[]>([]);
  const [journeyLength, setJourneyLength] = useState<number>(0);
  const [boredomLevel, setBoredomLevel] = useState<number>(0);
  const [travelResultsVisible, setTravelResultsVisible] = useState(false);
  const [travelResultConfig, setTravelResultConfig] = useState<
    TravelResultConfig | undefined
  >();
  const [targetCharacter, setTargetCharacter] = useState<Character | Player | null>(null);
  const [nbTagClicks, setNbTagClicks] = useState(0);

  // Add useEffect to set up rewards on mount
  useEffect(() => {
    randomizeRewards();
  }, []);

  // Function to randomize reward positions
  const randomizeRewards = () => {
    // Possible journey sections (excluding FUTILE)
    const journeySections = ["SHORT", "GOOD", "GREAT", "EPIC"];
    
    // Choose big reward section (any except FUTILE)
    const bigRewardIndex = Math.floor(Math.random() * journeySections.length);
    const bigReward = journeySections[bigRewardIndex];
    
    // Set small reward sections (before and after big reward)
    let smallRewards: string[] = [];
    
    if (bigReward === "EPIC") {
      // If EPIC is big reward, small rewards go on GOOD and GREAT
      smallRewards = ["GOOD", "GREAT"];
    } else if (bigReward === "SHORT") {
      // If SHORT is big reward, small rewards go on GOOD and FUTILE
      smallRewards = ["FUTILE", "GOOD"];
    } else {
      // For GOOD or GREAT, take the sections before and after
      const prevIndex = Math.max(0, bigRewardIndex - 1);
      const nextIndex = Math.min(journeySections.length - 1, bigRewardIndex + 1);
      smallRewards = [
        journeySections[prevIndex], 
        journeySections[nextIndex]
      ].filter(section => section !== bigReward);
      
     
    }
    
    setBigRewardSection(bigReward);
    setSmallRewardSections(smallRewards);
  };

  const getJourneyDetails = (score: number): JourneyLength => {
    if (score >= 80) return { rating: "EPIC", score: 100 };
    if (score >= 60) return { rating: "GREAT", score: 75 };
    if (score >= 40) return { rating: "GOOD", score: 50 };
    if (score >= 20) return { rating: "SHORT", score: 25 };
    return { rating: "FUTILE", score: 0 };
  };

  // Add these helper functions at the top of your component
  const getBoredomLevel = (value: number): string => {
    if (value >= 66) return "HIGH";
    if (value >= 33) return "MEDIUM";
    return "LOW";
  };

  const getTensionLevel = (value: number): string => {
    if (value >= 66) return "ZERO";
    if (value >= 33) return "LOW";
    return "BASIC";
  };

  const handleTravelOption = (option: TravelOption) => {
    setBoredomLevel(Math.min(boredomLevel + option.boredom, 100));
    setJourneyLength(Math.min(journeyLength + option.time, 100));
    
    if(!option.repeatable) {
       setUsedTravelOptionIds(prev => [...prev, option.id]);
    }
    
    const travelOptionResult = campaignManager.selectTravelOption(
      option,
      boredomLevel
    );

    // Find the first travel result that has an 'object' property (character or player)
    const travelOptionResultCharacter = travelOptionResult?.travelResults.find(
      (result) => (result?.object && result.object !== 'party')
    )?.object;
    if (travelOptionResultCharacter && travelOptionResultCharacter !== 'party') {
      setTargetCharacter(travelOptionResultCharacter);
    } else {
      setTargetCharacter(null);
    }

    const travelResults = travelOptionResult?.travelResults || [];
    const travelTags = travelOptionResult?.travelTags || [];
    setSelectedOption(option);
    setTravelResultConfig({
      results: travelResults,
      title: option.name,
      description: option.description,
      tags: travelTags,
      travelOption: option,
      info: {
        boredom: option.boredom,
        score: option.score,
        time: option.time,
        exhaustion: option.exhaustion || 0,
      },
    });
    setNbTagClicks(option.tags);
    setTravelResultsVisible(true);
  };
  const handleAcceptJourney = () => {
  //  campaignManager.addCampaignScore(journeyLength);
    // Handle boredom effects on tension
    if (boredomLevel > 66) {
      partyManager.multiplyPartyTension(0);
    } else if (boredomLevel > 33) {
      partyManager.multiplyPartyTension(0.5);
    } else {
      partyManager.multiplyPartyTension(0.75);
    }
    
    // Award DM points based on journey reward sections
    const currentRating = getJourneyDetails(journeyLength).rating;
    
    // Determine reward amount
    let dmPoints = 0;
    if (currentRating === bigRewardSection) {
      dmPoints = 2; // Big reward = 2 points
      campaignManager.addDmExp(2);
   //   uiManager.showToast(`Awarded 2 DM points for reaching ${currentRating} journey milestone!`);
    } else if (smallRewardSections.includes(currentRating)) {
      dmPoints = 1; // Small reward = 1 point
      campaignManager.addDmExp(1);
    //  uiManager.showToast(`Awarded 1 DM point for reaching ${currentRating} journey milestone!`);
    }
    
    campaignManager.backToOverview();
  };

  const handleTagClick = (flavorTag: EncounterFlavorTag) => {
    if (nbTagClicks > 0) {
      setNbTagClicks(nbTagClicks - 1);
    }

    switch(selectedOption?.travelOptionType) {
      case TRAVEL_OPTION.CHARACTER_BACKSTORY:
         characterManager.assignBackstory(targetCharacter as Character, flavorTag.tag);
        break;
      default:
        campaignManager.exposeTagForEncounter(flavorTag);
    }
    setSelectedTags([...selectedTags, flavorTag.tag]); 
  };

  // Create a function to filter out used options
const getFilteredOptions = (options: TravelOption[], count: number) => {
  const filtered = options
    .filter(option => !usedTravelOptionIds.includes(option.id))
    .sort(() => 0.5 - Math.random());
    
  // If we have enough fresh options, return them

    return filtered.slice(0, Math.min(count, filtered.length));

  
};
const shortTravelOptions = getFilteredOptions(travelOptions[0], 3);
const mediumTravelOptions = getFilteredOptions(travelOptions[1], 3);
const longTravelOptions = getFilteredOptions(travelOptions[2], 3);

  const renderTravelResults = () => {
    if (!travelResultConfig) return null;
    return (
      <div className="results-panel">
        <div className="results-panel-container">
          <div className="results-panel-header">
            {travelResultConfig.title}
            <div className="results-panel-description">
              {travelResultConfig.description}
            </div>
            <button
              className="close-button"
              onClick={() => setTravelResultsVisible(false)}
            >
              ×
            </button>
          </div>

          <div className="travel-result-info"> <div className="travel-result-info-item boredom">
            <TooltipWrapper content="Boredom risk is a measure of player patience and quality of the event.">
             
               👁️ Boredom Risk : +{travelResultConfig.info.boredom}
             
            </TooltipWrapper> </div> <div className="travel-result-info-item time">
            <TooltipWrapper content="Journey Length is an indication of time passing in game.">
             
              🕒 Journey Length : +{travelResultConfig.info.time}
             
            </TooltipWrapper> </div>
           
          </div>

          <div className="travel-results">
            {travelResultConfig.results.map((result, idx) => (
              <EffectDisplay key={idx} effectData={result} />
            ))}
          </div>
          {travelResultConfig.tags.length > 0 && nbTagClicks > 0 && (            <div className= "travel-tag-container">              <div className ="clicks-remaining">
                <TooltipWrapper content="Click on the tags to reveal them." maxWidth="200" preservePositioning={true}>
                  {nbTagClicks}
                </TooltipWrapper>
              </div>
              <div className ="travel-tags-instructions">
                {travelResultConfig.travelOption.tagInstructions}
                </div>
              <div className="travel-tags-container">
                
                <div className="travel-tags">
                  {travelResultConfig.tags.map((tag) => {
                    const tagUsed = selectedTags.includes(tag.tag);
                    const tagDisplayTheme = "location-tag"
                  return (<FlavorTagPill
                      anyFlavorTag={tag}
                      onClick={handleTagClick}
                      className={`${tagDisplayTheme} ${tagUsed ? "used" : ""}`}
                    />)
                })}
                </div>
              </div>
            </div>
          )}

          <div className="bottom-container">
            <div
              className="return-button"
              onClick={() => setTravelResultsVisible(false)}
            >
              Return to Travel Options
            </div>
          </div>
        </div>
      </div>
    );
  };

  const renderTravelOptions = () => {
    return (
      <div className="travel-options-panel">
        <div className="travel-options-container short">
          <div className="travel-section-title">Short Tasks</div>
          <div className="travel-options">
            {shortTravelOptions.map((option) => (
              <TravelOptionDisplay
                key={option.id}
                travelOption={option}
                onClick={() => handleTravelOption(option)}
                length={TRAVEL_LENGTH.SHORT}
              />
            ))}
          </div>
        </div>

        <div className="travel-options-container medium">
          <div className="travel-section-title">Moderate Events</div>
          <div className="travel-options">
            {mediumTravelOptions.map((option) => (
              <TravelOptionDisplay
                key={option.id}
                travelOption={option}
                onClick={() => handleTravelOption(option)}
                length={TRAVEL_LENGTH.MEDIUM}
              />
            ))}
          </div>
        </div>

        <div className="travel-options-container long">
          <div className="travel-section-title">Journey Happenings</div>
          <div className="travel-options">
            {longTravelOptions.map((option) => (
              <TravelOptionDisplay
                key={option.id}
                travelOption={option}
                onClick={() => handleTravelOption(option)}
                length={TRAVEL_LENGTH.LONG}
              />
            ))}
          </div>
        </div>
      </div>
    );
  };

  return (
    <div className="game-encounter-travel-container">
      <div className="encounter-type-title">Travel</div>
      <div className="game-encounter-travel">
        <div className="travel-meter-container">
          <div className="travel-sections">
            <div className="travel-meter-name">BOREDOM RISK</div>
            <div
              className={`travel-section long ${
                getBoredomLevel(boredomLevel) === "HIGH" ? "active" : ""
              }`}
            >
              <span className="section-label">HIGH</span>
              <span className="section-effect">All players getting bored</span>
            </div>
            <div
              className={`travel-section medium ${
                getBoredomLevel(boredomLevel) === "MEDIUM" ? "active" : ""
              }`}
            >
              <span className="section-label">MEDIUM</span>
              <span className="section-effect">Several players fidgeting</span>
            </div>
            <div
              className={`travel-section short ${
                getBoredomLevel(boredomLevel) === "LOW" ? "active" : ""
              }`}
            >
              <span className="section-label">LOW</span>
              <span className="section-effect">
                Players mostly paying attention
              </span>
            </div>
          </div>
          <div className="travel-thermometer">
            <span className="meter-icon">👁️</span>
            <div className="meter-fill" style={{ height: `${boredomLevel}%` }}>
              <span className="meter-value">{boredomLevel}</span>
            </div>
            <div className="meter-markers">
              {[0, 25, 50, 75, 100].map((mark) => (
                <div key={mark} className="marker">
                  <span className="marker-value">{mark}</span>
                </div>
              ))}
            </div>
          </div>

          <div className="travel-sections">
            <div className="travel-meter-name">TENSION</div>
            <div
              className={`travel-section zero ${
                getTensionLevel(boredomLevel) === "ZERO" ? "active" : ""
              }`}
            >
              <span className="section-label details">Attention Loss</span>
              <span className="section-label details">15% Attitude Loss</span>
              <span className="section-label details">-10 Tension</span>
            </div>
            <div
              className={`travel-section low ${
                getTensionLevel(boredomLevel) === "LOW" ? "active" : ""
              }`}
            >
              <span className="section-label details">50% Attention Loss</span>
              <span className="section-label details">5% Attitude Loss</span>
              <span className="section-label details">-10 Tension</span>
            </div>
            <div
              className={`travel-section basic ${
                getTensionLevel(boredomLevel) === "BASIC" ? "active" : ""
              }`}
            >
              <span className="section-label details">
                20% Chance of Boredom
              </span>
              <span className="section-label details">-5 Tension</span>
            </div>
          </div>
        </div>

        {travelResultsVisible ? renderTravelResults() : renderTravelOptions()}
      </div>

      <div className="travel-meter-label"> 🕒 JOURNEY LENGTH</div>
      <div className="bottom-meter-container">          <div className="travel-meter-container">          
             <TooltipWrapper content="Journey Length is a measure of time in-game. Rewards are given for longer journeys, and for ending journeys on the indicated sections." preservePositioning={false}>
            <div className="travel-sections">
              {["FUTILE", "SHORT", "GOOD", "GREAT", "EPIC"].map((rating) => (
                <div
                  key={rating}
                  className={`travel-section ${rating.toLowerCase()} ${
                    rating === getJourneyDetails(journeyLength).rating
                      ? "active"
                      : ""
                  }`}
                >
                  <span className="section-label">
                    {rating}
                    {rating === bigRewardSection && (
                      <span className="dm-points reward big-reward">🎲🎲</span>
                    )}
                    {smallRewardSections.includes(rating) && (
                      <span className="dm-points reward small-reward">🎲</span>
                    )}
                  </span>
                </div>
              ))}
            </div>
        </TooltipWrapper>
            <div className="travel-thermometer journey">
              <div className="meter-fill" style={{ width: `${journeyLength}%` }}>
                <span className="meter-value">{journeyLength}</span>
              </div>
              <div className="meter-markers">
                {[0, 20, 40, 60, 80, 100].map((mark) => (
                  <div key={mark} className="marker">
                    <span className="marker-value">{mark}</span>
                  </div>
                ))}
              </div>
            </div>
          </div>

        <div className="accept-journey-button" onClick={handleAcceptJourney}>
          Accept {getJourneyDetails(journeyLength).rating.toUpperCase()} journey
          <span className="journey-score">
            +{getJourneyDetails(journeyLength).score} Campaign Score
          </span>
        </div>
      </div>
    </div>
  );
};

export default GameEncounterTravel;
