import React, { useState, useEffect, useCallback } from "react";
import "./SlotMachine.scss";
import { PAYLINES, SYMBOL_PAYOUTS, BET_OPTIONS, STORAGE_KEYS, DEFAULT_CREDITS, DEFAULT_WILDS, SYMBOLS, WILD_PROBABILITY } from "../../consts/slotMachineConfig";
import { loadFromLocalStorage, saveToLocalStorage } from "../../utils/localStorage";
import { SLOT_MACHINE_SOUNDS, playSound, preloadSounds } from "../../utils/sounds";

interface SlotMachineProps {
	onClose?: () => void;
}

interface WildAnimation {
	id: string;
	row: number;
	col: number;
}

interface Position {
	row: number;
	col: number;
}

const SlotMachine: React.FC<SlotMachineProps> = ({ onClose }) => {
	const [credits, setCredits] = useState(() =>
		loadFromLocalStorage(STORAGE_KEYS.SLOT_MACHINE_CREDITS, DEFAULT_CREDITS)
	);
	const [wilds, setWilds] = useState(() =>
		loadFromLocalStorage(STORAGE_KEYS.SLOT_MACHINE_WILDS, DEFAULT_WILDS)
	);
	const [currentBet, setCurrentBet] = useState(BET_OPTIONS[0]);
	const [highlightWins, setHighlightWins] = useState(false);
	const [winningPositions, setWinningPositions] = useState<Position[]>([]);
	const [currentGrid, setCurrentGrid] = useState<string[][]>([]);
	const [showDebugPanel, setShowDebugPanel] = useState(false);
	const [winAmount, setWinAmount] = useState(0);
	const [message, setMessage] = useState("");
	const [isSpinning, setIsSpinning] = useState(false);
	const [bigWinActive, setBigWinActive] = useState(false);
	const [isWildStormActive, setIsWildStormActive] = useState(false);
	const [wildStormChancePercentage, setWildStormChancePercentage] = useState(5);
	const [wildAnimations, setWildAnimations] = useState<WildAnimation[]>([]);
	const [activePaylineIndex, setActivePaylineIndex] = useState<number>(-1);
	const [highlightInterval, setHighlightInterval] = useState<number | null>(null);
	const [paylinesToHighlight, setPaylinesToHighlight] = useState<Position[][]>([]);

	// Save credits to localStorage whenever it changes
	useEffect(() => {
		saveToLocalStorage(STORAGE_KEYS.SLOT_MACHINE_CREDITS, credits);
	}, [credits]);

	// Save wilds to localStorage whenever it changes
	useEffect(() => {
		saveToLocalStorage(STORAGE_KEYS.SLOT_MACHINE_WILDS, wilds);
	}, [wilds]);

	// Cleanup function for highlight interval
	useEffect(() => {
		return () => {
			if (highlightInterval !== null) {
				clearInterval(highlightInterval);
			}
		};
	}, [highlightInterval]);

	const startPaylineCycle = useCallback((paylineGroups: Position[][]) => {
		if (highlightInterval !== null) {
			clearInterval(highlightInterval);
		}
		setPaylinesToHighlight(paylineGroups);
		setActivePaylineIndex(0);

		const interval = window.setInterval(() => {
			setActivePaylineIndex(prev => (prev + 1) % paylineGroups.length);
		}, 1000);

		setHighlightInterval(interval);
	}, [highlightInterval]);

	const getSymbolName = useCallback((symbol: string): string => {
		switch (symbol) {
			case "🛡️": return "Shield";
			case "💍": return "Ring";
			case "✨": return "Wild";
			case "J": return "Jack";
			case "Q": return "Queen";
			case "K": return "King";
			case "A": return "Ace";
			default: return symbol;
		}
	}, []);

	const generateRandomGrid = useCallback(() => {
		const newGrid: string[][] = [];
		for (let row = 0; row < 4; row++) {
			newGrid[row] = [];
			for (let col = 0; col < 5; col++) {
				if (col === 4) {
					const specialRoll = Math.random();
					if (specialRoll < 0.2) {
						newGrid[row][col] = "✨";
					} else if (specialRoll < 0.5) {
						const premiumSymbols = ["K", "A", "🛡️", "💍"];
						newGrid[row][col] = premiumSymbols[Math.floor(Math.random() * premiumSymbols.length)];
					} else {
						const regularSymbols = SYMBOLS.filter((s) => s !== "✨");
						newGrid[row][col] = regularSymbols[Math.floor(Math.random() * regularSymbols.length)];
					}
				} else {
					if (Math.random() * 100 < WILD_PROBABILITY) {
						newGrid[row][col] = "✨";
					} else {
						const symbolsWithoutWild = SYMBOLS.filter((s) => s !== "✨");
						newGrid[row][col] = symbolsWithoutWild[Math.floor(Math.random() * symbolsWithoutWild.length)];
					}
				}
			}
		}
		return newGrid;
	}, []);

	const animateWildCollection = useCallback((row: number, col: number) => {
		const id = `wild-${row}-${col}-${Date.now()}`;
		setWildAnimations(prev => [...prev, { id, row, col }]);
		setTimeout(() => {
			setWildAnimations(prev => prev.filter(anim => anim.id !== id));
		}, 1000);
	}, []);

	const checkForWinsDigital = useCallback((grid: string[][]) => {
		const betMultiplier = currentBet / (BET_OPTIONS[0] || 1);
		let totalWinnings = 0;
		let winMessages: string[] = [];
		let collectedWildsThisSpin = 0;
		let allWinningPositionsThisSpin: Position[] = [];
		let winningPaylineGroups: Position[][] = [];

		PAYLINES.forEach((payline, paylineIndex) => {
			let currentSymbol = "";
			let dominantSymbol = "";
			let currentStreak = 0;
			let paylineWinningPositions: Position[] = [];
			let paylineSymbols: string[] = [];

			for (let i = 0; i < payline.length; i++) {
				const pos = payline[i];
				if (!grid[pos.row] || !grid[pos.row][pos.col]) {
					console.error(`Invalid position in payline ${paylineIndex + 1}: row ${pos.row}, col ${pos.col}. Grid:`, grid);
					continue;
				}
				const symbol = grid[pos.row][pos.col];

				if (i === 0) {
					currentSymbol = symbol;
					if (symbol !== "✨") {
						dominantSymbol = symbol;
					}
					currentStreak = 1;
					paylineWinningPositions.push(pos);
					paylineSymbols.push(symbol);
				} else {
					if (symbol === currentSymbol || symbol === "✨" || currentSymbol === "✨") {
						currentStreak++;
						paylineWinningPositions.push(pos);
						paylineSymbols.push(symbol);
						if (currentSymbol === "✨" && symbol !== "✨") {
							currentSymbol = symbol;
							dominantSymbol = symbol;
						} else if (symbol !== "✨" && dominantSymbol === "") {
							dominantSymbol = symbol;
						}
					} else {
						break;
					}
				}
			}

			if (currentStreak >= 3) {
				const payoutSymbolToUse = dominantSymbol || currentSymbol;
				let baseWinningAmount = 0;

				const payoutConfig = SYMBOL_PAYOUTS[payoutSymbolToUse as keyof typeof SYMBOL_PAYOUTS];
				if (payoutConfig) {
					baseWinningAmount = payoutConfig[currentStreak as keyof typeof payoutConfig] || 0;
				}

				if (baseWinningAmount > 0) {
					const winningAmount = Math.floor(baseWinningAmount * betMultiplier);
					totalWinnings += winningAmount;
					allWinningPositionsThisSpin.push(...paylineWinningPositions.slice(0, currentStreak));
					winningPaylineGroups.push(paylineWinningPositions.slice(0, currentStreak));
					const symbolNames = paylineSymbols.slice(0, currentStreak).map(getSymbolName);
					winMessages.push(
						`Payline ${paylineIndex + 1}: ${currentStreak} ${getSymbolName(payoutSymbolToUse)}! ` +
						`[${symbolNames.join(", ")}] +${winningAmount}`
					);
					if (winningAmount >= 50) playSound(SLOT_MACHINE_SOUNDS.WIN_BIG, 0.6);
					else if (winningAmount >= 20) playSound(SLOT_MACHINE_SOUNDS.WIN_MEDIUM, 0.5);
					else playSound(SLOT_MACHINE_SOUNDS.WIN_SMALL, 0.5);
				}
			}
		});

		for (let row = 0; row < grid.length; row++) {
			for (let col = 0; col < grid[row].length; col++) {
				if (grid[row][col] === "✨") {
					collectedWildsThisSpin++;
					if (!isWildStormActive) {
						animateWildCollection(row, col);
					}
				}
			}
		}

		if (collectedWildsThisSpin > 0 && !isWildStormActive) {
			setWilds(prev => prev + collectedWildsThisSpin);
			winMessages.push(`Collected ${collectedWildsThisSpin} wild symbol${collectedWildsThisSpin > 1 ? "s" : ""}!`);
		}

		setWinningPositions(allWinningPositionsThisSpin);
		setHighlightWins(allWinningPositionsThisSpin.length > 0);

		if (winningPaylineGroups.length > 0) {
			startPaylineCycle(winningPaylineGroups);
		}

		if (totalWinnings > 0) {
			setWinAmount(totalWinnings);
			setCredits(prev => prev + totalWinnings);
			setMessage(winMessages.join("\n"));
			if (totalWinnings >= 50 && !bigWinActive) {
				setBigWinActive(true);
				setTimeout(() => setBigWinActive(false), 3000);
			}
		} else {
			if (collectedWildsThisSpin > 0 && !isWildStormActive) {
				setMessage(winMessages.join("\n") || "Collected wilds!");
			} else if (!isWildStormActive) {
				setMessage("Try again!");
			}
		}
	}, [currentBet, getSymbolName, setWilds, setCredits, setWinAmount, setMessage, setWinningPositions, setHighlightWins, bigWinActive, isWildStormActive, animateWildCollection, startPaylineCycle]);

	const spin = useCallback(async () => {
		if (isSpinning || credits < currentBet) return;

		if (highlightInterval !== null) {
			clearInterval(highlightInterval);
			setHighlightInterval(null);
		}

		setIsSpinning(true);
		setWinAmount(0);
		setMessage("");
		setCredits(prev => prev - currentBet);
		setWinningPositions([]);
		setHighlightWins(false);
		setActivePaylineIndex(-1);
		setPaylinesToHighlight([]);

		playSound(SLOT_MACHINE_SOUNDS.SPIN, 0.4);

		const animationTotalDuration = 600;
		const animationFrameInterval = 75;
		const numAnimationFrames = Math.floor(animationTotalDuration / animationFrameInterval);

		for (let i = 0; i < numAnimationFrames; i++) {
			setCurrentGrid(generateRandomGrid());
			await new Promise(r => setTimeout(r, animationFrameInterval));
		}

		let activeGrid = generateRandomGrid();
		setCurrentGrid([...activeGrid]);

		await new Promise((resolve) => setTimeout(resolve, 200));

		let wildstormHappened = false;
		const wildStormRoll = Math.random();
		if (wilds > 0 && wildStormRoll < wildStormChancePercentage / 100) {
			setIsWildStormActive(true);
			wildstormHappened = true;
			playSound(SLOT_MACHINE_SOUNDS.WILD_STORM_TRIGGER, 0.7);
			setMessage("WILD STORM!");

			let currentWildsCount = wilds;
			let tempGrid = activeGrid.map(row => [...row]);

			for (let i = 0; i < currentWildsCount; i++) {
				const availableSpots: Position[] = [];
				for (let r = 0; r < tempGrid.length; r++) {
					for (let c = 0; c < tempGrid[r].length; c++) {
						if (tempGrid[r][c] !== "✨") {
							availableSpots.push({ row: r, col: c });
						}
					}
				}

				if (availableSpots.length === 0) break;

				const randomSpot = availableSpots[Math.floor(Math.random() * availableSpots.length)];
				tempGrid[randomSpot.row][randomSpot.col] = "✨";

				setWilds(prevWilds => Math.max(0, prevWilds - 1));
				setCurrentGrid([...tempGrid]);
				playSound(SLOT_MACHINE_SOUNDS.WILD_APPLIED, 0.5);

				await new Promise((resolve) => setTimeout(resolve, 300));
			}
			setWilds(0);
			activeGrid = [...tempGrid];
			setIsWildStormActive(false);
			setMessage("Wild Storm Complete! Checking wins...");
			await new Promise((resolve) => setTimeout(resolve, 1000));
		}

		checkForWinsDigital(activeGrid);

		if (wildstormHappened) {
			setWilds(5);
		}

		setIsSpinning(false);
	}, [isSpinning, credits, currentBet, wilds, generateRandomGrid, checkForWinsDigital, wildStormChancePercentage, highlightInterval]);

	// Preload sounds when component mounts
	useEffect(() => {
		preloadSounds();
	}, []);

	// Initialize grid on mount
	useEffect(() => {
		setCurrentGrid(generateRandomGrid());
	}, [generateRandomGrid]);

	return (
		<div className="slot-machine-container">
			<div className="slot-machine-header">
				<h2>Regal Richez</h2>
				<div className="credits-display">Credits: ${credits}</div>
			</div>
			<div className="slot-machine-display-row">
				<div className="slot-machine-content">
					<div className="slot-machine-body">
						<div className="slot-machine-main-area">
							<div className="debug-config-panel">
								<h4>Debug Config</h4>
								<div className="config-item">
									<label htmlFor="wildStormChance">Wild Storm Chance (%):</label>
									<input 
										type="number"
										id="wildStormChance"
										value={wildStormChancePercentage}
										onChange={(e) => setWildStormChancePercentage(Math.max(0, Math.min(100, parseInt(e.target.value, 10))))}
										min="0"
										max="100"
										style={{ 
											appearance: 'none',
											MozAppearance: 'textfield',
											WebkitAppearance: 'none'
										}}
									/>
								</div>
							</div>

							<div className={`slot-display ${bigWinActive ? "big-win-active" : ""} ${isWildStormActive ? "wild-storm-active" : ""}`}>
								<div className="wilds-container">
									<div className="wilds-label">WILDS</div>
									<div className="wilds-counter">{wilds}</div>
								</div>
								<div className="wild-collection-container">
									{wildAnimations.map(anim => (
										<div
											key={anim.id}
											className="wild-animation collecting-wild"
											style={{
												left: `${(anim.col * 20) + 10}%`,
												top: `${(anim.row * 20) + 10}%`
											}}
										>
											✨
										</div>
									))}
								</div>
								<div className="debug-grid">
									<div className="col-labels">
										<div className="corner-placeholder"></div>
										{[1, 2, 3, 4, 5].map((colNum) => (
											<div key={`col-label-${colNum}`} className="col-label">
												Col {colNum}
											</div>
										))}
									</div>
									{currentGrid.map((row, rowIndex) => (
										<div key={`row-${rowIndex}`} className="debug-row">
											<div className="row-label">Row {rowIndex + 1}:</div>
											{row.map((symbol, colIndex) => {
												const isWinningPosition = winningPositions.some(
													pos => pos.row === rowIndex && pos.col === colIndex
												);

												const isHighlightedPosition = 
													paylinesToHighlight[activePaylineIndex]?.some(
														pos => pos.row === rowIndex && pos.col === colIndex
													) || false;

												let symbolName = getSymbolName(symbol);
												let symbolClass = "";
												if (["J", "Q", "K", "A"].includes(symbol)) {
													symbolClass = ["J", "A"].includes(symbol)
														? "card-red"
														: "card-black";
												} else if (symbol === "✨") {
													symbolClass = "wild-symbol";
												}

												return (
													<div
														key={`cell-${rowIndex}-${colIndex}`}
														className={`debug-cell ${symbolClass} ${
															isWinningPosition ? "winning" : ""
														} ${isHighlightedPosition ? "highlight-active" : ""}`}
														title={`Row ${rowIndex + 1}, Col ${
															colIndex + 1
														}: ${symbolName}`}
													>
														{symbol}
													</div>
												);
											})}
										</div>
									))}
								</div>
							</div>

							{showDebugPanel && (
								<div className="debug-panel">
									<h4>Win Details</h4>
									{message && (
										<div className="debug-message">
											{message.split("\n").map((line, i) => (
												<p key={`msg-${i}`}>{line}</p>
											))}
										</div>
									)}
								</div>
							)}
						</div>
					</div>

					<div className="slot-machine-controls">
						<div className="bet-controls">
							<label htmlFor="bet-select">Bet:</label>
							<select
								id="bet-select"
								value={currentBet}
								onChange={(e) => setCurrentBet(Number(e.target.value))}
								disabled={isSpinning}
							>
								{BET_OPTIONS.map((bet) => (
									<option key={bet} value={bet}>
										${bet}
									</option>
								))}
							</select>
						</div>
						<button
							className="spin-button"
							onClick={spin}
							disabled={isSpinning || credits < currentBet}
						>
							{isSpinning ? "Spinning..." : "Spin"}
						</button>
						<button
							className="spin-button details"
							onClick={() => setShowDebugPanel(!showDebugPanel)}
						>
							{showDebugPanel ? "Hide Details" : "Show Details"}
						</button>
					</div>
				</div>
			</div>
		</div>
	);
};

export default SlotMachine;
