import React, { useState } from 'react';
import SlotMachine from './SlotMachine';
import './SlotMachineModal.scss';

interface SlotMachineModalProps {
  isOpen: boolean;
  onClose: () => void;
}

const SlotMachineModal: React.FC<SlotMachineModalProps> = ({ isOpen, onClose }) => {
  if (!isOpen) return null;

  return (
    <div className="slot-machine-modal" onClick={(e) => {
      // Close the modal only if clicking on the backdrop, not the content
    //   if (e.target === e.currentTarget) {
    //     onClose();
    //   }
    }}>
      <div className="slot-machine-modal-content">
        <div className="casino-sign">
				<h1>CHOMPY LOUNGE</h1>
				<div className="sign-subtitle">PREMIUM GAMING</div>
			</div>
        <SlotMachine onClose={onClose} />
      </div>
    </div>
  );
};

export default SlotMachineModal;
