import { LEVELUP_OPTION } from "../../consts/general";
import { useModal } from "../../hooks/useModal";
import CImage from "./cimage";
import EffectDisplay from "./effectDisplay";
import './Modal.scss';
 

export const Modal: React.FC = () => {
    const { modalConfig, handleOptionSelect, closeModal } = useModal();
  
    if (!modalConfig) return null;
  

    if(modalConfig.type === 'levelup') {
      return (
        <div className="modal-overlay">
          <div className="modal-container">
            <div className="modal-header">
              <h2>{modalConfig.title}</h2>
              <button className="close-button" onClick={closeModal}>×</button>
            </div>
            <div className="modal-options">
              {modalConfig.options.map((option) => (
                <button
                  key={option.id}
                  className={`modal-option ${option.class || ''}`}
                  onClick={() => handleOptionSelect(option.id)}
                >
                  <div className="option-content">
                  <h3>{option.title}</h3>
                  </div>
                  {option.icon && (
                    option.id === LEVELUP_OPTION.CHARACTER ? 
                      <CImage imageId={option.icon} /> 
                    : 
                      <span className="option-icon">{option.icon}</span>
                  )}
                  <div className="option-content">
                  
                    {option.description && <p>{option.description}</p>}
                  </div>
                </button>
              ))}
            </div>
          </div>
        </div>
      );
    }

    if(modalConfig.type === 'travelresult' && modalConfig.travelConfig) {
      return (
        <div className="modal-overlay">
          <div className="modal-container travel">
            <div className="modal-header">
              {modalConfig.title}
              <button className="close-button" onClick={closeModal}>×</button>
            </div>
            <div className="travel-results">

            
              {modalConfig.travelConfig.results.map((result) => (
                  <EffectDisplay
                    effectData={result}
                    />
              ))}
            </div>
            <div className="travel-tags">
              {modalConfig.options.map((option) => (
                // These are always tags? 
                <div>{option.id}</div>
              ))}
            </div>
          </div>
        </div>
      );
    }



    return (
      <div className="modal-overlay">
        <div className="modal-container">
          <div className="modal-header">
            <h2>{modalConfig.title}</h2>
            <button className="close-button" onClick={closeModal}>×</button>
          </div>
          <div className="modal-options">
            {modalConfig.options.map((option) => (
              <button
                key={option.id}
                className={`modal-option ${option.class || ''}`}
                onClick={() => handleOptionSelect(option.id)}
              >
                <div className="option-content">
                <h3>{option.title}</h3>
                </div>
                {option.icon && (
                  option.id === LEVELUP_OPTION.CHARACTER ? 
                    <CImage imageId={option.icon} /> 
                  : 
                    <span className="option-icon">{option.icon}</span>
                )}
                <div className="option-content">
                
                  {option.description && <p>{option.description}</p>}
                </div>
              </button>
            ))}
          </div>
        </div>
      </div>
    );
  };