import React, { useState, useEffect } from 'react';
import { Encounter, Character, Tag, TownBuilding, BUILDING_TYPE, EncounterFlavorTag, BuildingEffect, BUILDING_EFFECT_TYPE, Building } from '../../models';

import './town.scss';
import { useAppManager, useCampaignManager, useCharacterManager } from '../managerContexts';
import BuildingCard from './buildingCard';
import CharacterToken from './characterToken';
import { DEFAULT_TOWN_BUILDINGS } from '../../consts/buildings';

interface GameEncounterTownProps {
  encounter: Encounter;
}

const GameEncounterTown: React.FC<GameEncounterTownProps> = ({ encounter }) => {
	const appManager = useAppManager();
	const townManager = appManager.getTownManager();
	const characterManager = useCharacterManager();
  const [buildings, setBuildings] = useState<TownBuilding[]>([]);
  const [selectedCharacter, setSelectedCharacter] = useState<Character | null>(null);
  const [characters, setCharacters] = useState<Character[]>(characterManager.getCharacters());
  const [exhaustion, setExhaustion] = useState<number>(0);
  const [assignedCharacters, setAssignedCharacters] = useState<Set<string>>(new Set());
  const [animatingCharacter, setAnimatingCharacter] = useState<{
	character: Character;
	from: DOMRect;
	to: DOMRect;
  } | null>(null);
  const [autoAssign, setAutoAssign] = useState<boolean>(true);
  const campaignManager = useCampaignManager();
  
 // Add this helper function
const getAvailableBuildings = () => {
	return buildings.filter(b => b.currentVisitors.length < b.capacity);
  };
  
  const generateTownBuildings = (encounter: Encounter): TownBuilding[] => {
	const buildingTypes = Object.values(BUILDING_TYPE);
	const generatedBuildings: TownBuilding[] = [];
	
	// Random number of buildings between 4 and 7
	const minBuildings = encounter.town?.[0]?.nbBuildings[0] || 4;
	const maxBuildings = encounter.town?.[0]?.nbBuildings[1] || 7;

	const numBuildings = minBuildings + Math.floor(Math.random() * (maxBuildings - minBuildings + 1));  
	
	// Create a grid system to help with positioning
	const gridCellsX = 4; // 4x4 grid
	const gridCellsY = 3; // 4x4 grid

	const cellWidth = 100 / gridCellsX;
	const cellHeight = 100 / gridCellsY;
	const usedCells: Set<string> = new Set();
  
	const availableBuildings = [...(encounter.town?.[0]?.buildings || DEFAULT_TOWN_BUILDINGS)];


	for (let i = 0; i < numBuildings; i++) {
	  // Pick random building type
	  const randomBuilding = availableBuildings[Math.floor(Math.random() * availableBuildings.length)];
	  // remove that building from the list
	  availableBuildings.splice(availableBuildings.indexOf(randomBuilding), 1);
  
	  // Find an unused grid cell
	  let cellX: number, cellY: number;
	  do {
		cellX = Math.floor(Math.random() * gridCellsX);
		cellY = Math.floor(Math.random() * gridCellsY);
	  } while (usedCells.has(`${cellX},${cellY}`));
	  
	  usedCells.add(`${cellX},${cellY}`);
  
	  // Add some randomness to position within cell
	  const x = (cellX * cellWidth) + (Math.random() * (cellWidth/4));
	  const y = (cellY * cellHeight) ;//+ (Math.random() * (cellHeight/4));
  
	  // Random size variations
	  const width = 200 + Math.random() * 100;
	  const height = 150 + Math.random() * 50;
  	  const capacity =  Math.floor(Math.random() * 2) + 1; // Random capacity between 1 and 3
	  const building: TownBuilding = {
		building: randomBuilding,
		tags: townManager.getTagsForBuilding(randomBuilding),
		resolvedTags: [],
		capacity,
		currentVisitors: [],
		effects: [...randomBuilding.effects],
		positionEffects: getPositionEffects(randomBuilding, capacity),
	//	score: Math.floor(Math.random() * 5) * 5 + 5,
		position: { 
		  x, 
		  y 
		},
		id: `${i}`, 
	  };

	  generatedBuildings.push(building);
	}
	return generatedBuildings;
  };

  
  const getPositionEffects = (building: Building, capacity: number): BuildingEffect[][] => {
	// Restrict position effects from using the same effect as the main building effect. 

	const randomEffect = () => {
		

		if(Math.random() <.5){
			return {type: BUILDING_EFFECT_TYPE.NONE, value: 0}
		}
		if(Math.random() < 0.3)  {
			switch(Math.floor(Math.random() * 9)) {
				case 0:
				case 1:
				case 2:
					if(building.effects.some((effect) => effect.type === BUILDING_EFFECT_TYPE.ADD_TENSION))
						break;
					return { type: BUILDING_EFFECT_TYPE.ADD_TENSION, value: Math.random() > .5 ? 5 : -5 };
				case 3:
				case 4:
					if(building.effects.some((effect) => effect.type === BUILDING_EFFECT_TYPE.ADD_ENJOYMENT))
						break;
					return { type: BUILDING_EFFECT_TYPE.ADD_ENJOYMENT, value: Math.random() > .5 ? 1 : -1 };
				case 5:
					if(building.effects.some((effect) => effect.type === BUILDING_EFFECT_TYPE.ADD_DM_POINT))
						break;
					return { type: BUILDING_EFFECT_TYPE.ADD_DM_POINT, value: 1 };
				case 6:
					if(building.effects.some((effect) => effect.type === BUILDING_EFFECT_TYPE.ADD_PLAYER_POINT))
						break;
					return { type: BUILDING_EFFECT_TYPE.ADD_PLAYER_POINT, value: 1 };
				case 7:
					if(building.effects.some((effect) => effect.type === BUILDING_EFFECT_TYPE.ADD_TREASURE))
						break;
					return { type: BUILDING_EFFECT_TYPE.ADD_TREASURE, value: 1 };
				case 8:
					if(building.effects.some((effect) => effect.type === BUILDING_EFFECT_TYPE.INJURE))
						break;
					if(building.effects.some((effect) => effect.type === BUILDING_EFFECT_TYPE.HEAL))
						break;
					return { type: BUILDING_EFFECT_TYPE.INJURE, value: 1 };
				default:
					return { type: BUILDING_EFFECT_TYPE.ADD_SCORE, value: 100 };
			}
		}
	
		if(Math.random() < 0.2) {
			return { type: BUILDING_EFFECT_TYPE.ADD_SCORE, value: 20 };
		} else {
			return { type: BUILDING_EFFECT_TYPE.ADD_SCORE, value: (Math.floor(Math.random() * 2) + 1) * 5};
		}
	}
	
	const positionEffects = Array.from({ length: capacity }, () => {
		return [randomEffect()];
	})

	return positionEffects;
}


  useEffect(() => {
    // Generate random buildings or use encounter-defined ones
    const generatedBuildings = generateTownBuildings(encounter);
    setBuildings(generatedBuildings);
  }, [encounter]);

// Add this new function
const assignCharacterToBuilding = (
	character: Character, 
	building: TownBuilding, 
	fromRect: DOMRect, 
	buildingRect: DOMRect
) => {
	
	
 // Calculate target slot position
 const slotIndex = building.currentVisitors.length;
 const toRect = getSlotPosition(buildingRect, slotIndex, building.capacity);
// Start animation
	setAnimatingCharacter({ character, from: fromRect, to: toRect });
 
	  // Force a reflow before applying transform
	  requestAnimationFrame(() => {
		const animatingElement = document.querySelector('.character-token.animating');
		if (animatingElement) {
		  // Set CSS variables for the transform
		  (animatingElement as HTMLElement).style.setProperty(
			'--move-x', 
			`${toRect.left - fromRect.left}px`
		  );
		  (animatingElement as HTMLElement).style.setProperty(
			'--move-y', 
			`${toRect.top - fromRect.top}px`
		  );
		  // Add moving class to trigger animation
		  animatingElement.classList.add('moving');
		}
	  });


	// Update assigned characters
	setAssignedCharacters(prev => new Set([...prev, String(character.id)]));
  
	// After animation completes, update building and apply effect
	setTimeout(() => {
	  setAnimatingCharacter(null);
	  
	  // Update building
	  const updatedBuildings = buildings.map(b => ({
		...b,
		isHighlighted: b.id === building.id,
		currentVisitors: b.id === building.id 
		  ? [...b.currentVisitors, character]
		  : b.currentVisitors
	  }));
	  setBuildings(updatedBuildings);
  
	  // highlight building
	  const buildingElement = document.querySelector(`[data-building-id="${building.id}"]`);
	  if (buildingElement) {
		buildingElement.classList.add('highlight');
		//setTimeout(() => buildingElement.classList.remove('highlight'), 500);
	  }
  
	  // Apply effect
	  setTimeout(() => {
		//townManager.BUILDING_EFFECTS[building.type].execute(character);
		townManager.townBuildingResolved(character, building, slotIndex);
	  }, 100);
	}, 1000); // Match this with CSS animation duration
  };

  // Add this helper function to calculate slot position
const getSlotPosition = (buildingRect: DOMRect, slotIndex: number, totalSlots: number) => {
	const slotWidth = 100; // Width of character token in slot
	const slotSpacing = 10; // Space between slots
	//const totalWidth = (slotWidth * totalSlots) + (slotSpacing * (totalSlots - 1));
	const startX = buildingRect.left + 42;//+ (buildingRect.width - totalWidth) / 2;
	const y = buildingRect.bottom - 122; // Position slots near bottom of building
  
	return new DOMRect(
	  startX + (slotWidth + slotSpacing) * slotIndex,
	  y,
	  slotWidth,
	  slotWidth
	);
  };
// Modify handleCharacterClick
const handleCharacterClick = (character: Character, elementRect: DOMRect) => {
	//if (assignedCharacters.has(String(character.id))) return;
  
	if (autoAssign) {
	  const availableBuildings = getAvailableBuildings();
	  if (availableBuildings.length > 0) {
		const randomBuilding = availableBuildings[Math.floor(Math.random() * availableBuildings.length)];
		const buildingElement = document.querySelector(`[data-building-id="${randomBuilding.id}"]`);
		if (buildingElement) {
		  const buildingRect = buildingElement.getBoundingClientRect();
		  assignCharacterToBuilding(character, randomBuilding, elementRect, buildingRect);
		  townManager.townBuildingAssigned(character, randomBuilding);
		  if(exhaustion > 0) {
			appManager.getPartyManager().exhaustParty(exhaustion);
		}
		  
		  setExhaustion(exhaustion + 2);
		}
	  }
	} else {
	  setSelectedCharacter(character);
	}
  };
// Modify handleBuildingClick
const handleBuildingClick = (building: TownBuilding, elementRect: DOMRect) => {
	if (!selectedCharacter || building.currentVisitors.length >= building.capacity) {
	  return;
	}
  
	const characterElement = document.querySelector(`[data-character-id="${selectedCharacter.id}"]`);
	if (characterElement) {
	  const characterRect = characterElement.getBoundingClientRect();
	  assignCharacterToBuilding(selectedCharacter, building, characterRect, elementRect);
	}
  
	setSelectedCharacter(null);
  };


  const handleTagClick = (flavorTag: EncounterFlavorTag, building: TownBuilding) => {
	
	
	
	//get the character in the last index
	const character = building.currentVisitors[building.currentVisitors.length - 1];

	appManager.getChatManager().townEvent( flavorTag,character, building.building);
	campaignManager.resolveTownTag(flavorTag);
	// Update buildings state to mark tag as resolved
	setBuildings(buildings.map(b => {
	  if (b.id === building.id) {
		return {
		  ...b,
		  isHighlighted: false,
		  resolvedTags: [...b.resolvedTags, flavorTag.tag]
		};
	  }
	  return b;
	}));
	const buildingElement = document.querySelector(`[data-building-id="${building.id}"]`);
	if (buildingElement) {
		buildingElement.classList.remove('highlight')
	}
  };




  return (
	<div className="town-encounter">
	  <div className="buildings-container">
		{buildings.map((building, index) => (
		  <BuildingCard 
			key={index}
			building={building}
			onClick={(e) => handleBuildingClick(building, e.currentTarget.getBoundingClientRect())}
			isSelectable={selectedCharacter !== null && 
						 building.currentVisitors.length < building.capacity}
			data-building-id={building.id}
			onTagClick={handleTagClick}
			onCharacterClick={handleCharacterClick} 
		  />
		))}
	  </div>
	  
	  <div className="characters-container">
		<div className="character-holder">

			
			<div className="characters-title">Encourage a character to act</div>
			<div className="characters-display">
				{exhaustion > 0 && <div className="exhaustion-title">Next action {exhaustion} Party Exhaustion</div>}
				<div className="characters-row">
						{characters
						//  .filter(char => !assignedCharacters.has(String(char.id)))
						.map((character, index) => (
							<CharacterToken
							key={index}
							character={character}
							isSelected={selectedCharacter === character}
							onClick={(e) => handleCharacterClick(character, e.currentTarget.getBoundingClientRect())}
							data-character-id={String(character.id)}
							
							/>
						))}
				</div>
						
			</div>
			
		</div>
	</div>
	  {animatingCharacter && (
  <CharacterToken
    character={animatingCharacter.character}
    style={{
      position: 'fixed',
      left: `${animatingCharacter.from.left}px`,
      top: `${animatingCharacter.from.top}px`,
      width: `${animatingCharacter.from.width}px`,
      height: `${animatingCharacter.from.height}px`,
      transition: 'transform 1.0s ease'  // Changed from 'all' to just 'transform'
    }}
    className="animating" // Add this class
    mini={true} // Add this to match destination size
    onClick={() => {}}
  />
)}
	</div>
  );
}

export default GameEncounterTown;