import React, { useState, useEffect, useRef } from 'react';
import { Tag, EncounterFlavorTag, RPEncounterFlavorTag, FlavorTagSource, FlavorTagTheme } from '../../models';
import { useAppManager, useCampaignManager, useUiManager } from '../managerContexts';
import './TagPill.scss';
import AppManager from '../../managers/appManager';

export interface TagPillProps {
  anyFlavorTag: EncounterFlavorTag | RPEncounterFlavorTag;
  disabled?: boolean;
  highlighted?: boolean;
  theme?: string;
  onClick?: (flavorTag: EncounterFlavorTag) => void;
  className?: string;
  showTooltip?: boolean;
  tooltipText?: string;
  off?: boolean;
}

const FlavorTagPill: React.FC<TagPillProps> = ({
  anyFlavorTag,
  disabled = false,
  highlighted = false,
  theme,
  onClick,
  className = '',
  showTooltip = true,
  tooltipText,
  off
}) => {
  const [tooltipVisible, setTooltipVisible] = useState(false);
  const tagRef = useRef<HTMLDivElement>(null);
  const campaignManager = useCampaignManager();
  const uiManager = useUiManager();
  const appManager = useAppManager();
  const tagManager = appManager.getTagManager();
  
  // Determine if this is a flavor tag or regular tag
  const isRPFlavorTag = 'character' in anyFlavorTag;
  
  // Get the actual Tag object regardless of input type
  const flavorTag: EncounterFlavorTag | undefined = isRPFlavorTag ? (anyFlavorTag as RPEncounterFlavorTag).tag : anyFlavorTag as EncounterFlavorTag;
  
  // Handle tooltip positioning relative to viewport edges
  useEffect(() => {
    if (tooltipVisible && tagRef.current) {
      const tooltip = tagRef.current.querySelector('.tooltip') as HTMLElement;
      if (tooltip) {
        const tagRect = tagRef.current.getBoundingClientRect();
        const tooltipRect = tooltip.getBoundingClientRect();
        
        // Adjust if tooltip extends beyond the viewport
        if (tooltipRect.right > window.innerWidth) {
          tooltip.style.left = 'auto';
          tooltip.style.right = '0';
          tooltip.style.transform = 'translateX(0)';
        }
        
        if (tooltipRect.left < 0) {
          tooltip.style.left = '0';
          tooltip.style.right = 'auto';
          tooltip.style.transform = 'translateX(0)';
        }
      }
    }
  }, [tooltipVisible]);

  // Generate tooltip content
  const getTooltipContent = (): string => {
   
    
    
      let content = '';
      
      // Get campaign tooltip if available
      const campaignTooltip = tagManager.getTooltipText({ tag: flavorTag, type: 'tag' });
      if (campaignTooltip) return campaignTooltip;
      
      // Otherwise build based on source
      switch (flavorTag?.source || "") {
        case FlavorTagSource.LOCATION:
          content = 'Location element';
          break;
        case FlavorTagSource.PLOT:
          content = 'Plot element';
          break;
        case FlavorTagSource.PLAYERS:
          content = 'Player preference';
          break;
        case FlavorTagSource.BACKSTORY:
          content = 'Character backstory element';
          break;
        case FlavorTagSource.FORESHADOWING:
          content = 'Foreshadowing of future events';
          break;
        default:
          content = flavorTag?.tag.name ?? 'Unknown tag';
          break;
      }
      
      // Add theme postfix if it exists
      if (flavorTag && flavorTag.theme && flavorTag.theme !== FlavorTagTheme.NONE) {
        content += ` (${flavorTag.theme})`;
      }
      
      return content;
    
    
  };

  // Determine CSS classes
  const getTagClasses = (): string => {
    let classes = 'tag-pill text-pill';
    
    // Handle tag type classes
   if(flavorTag) {
      if (flavorTag.source) {
        classes += ` type-${flavorTag.source.toLowerCase()}`;
      }
      if (flavorTag.theme && flavorTag.theme !== FlavorTagTheme.NONE) {
        classes += ` theme-${flavorTag.theme.toLowerCase()}`;
      }
       // Apply player attitude styles
    const playerAttitudeStyle = uiManager.getPlayerAttitudesStyle(flavorTag.tag);
    if (playerAttitudeStyle) classes += ` ${playerAttitudeStyle}`;
    }
    
    // Add conditional classes
    if (disabled) classes += ' disabled';
    if (highlighted) classes += ' highlighted';
    if (theme) classes += ` theme-${theme}`;
    if (className) classes += ` ${className}`;
    
    if (off) classes += ' off';
    
    return classes;
  };

  const handleClick = () => {
    if (!disabled && onClick && flavorTag) {
      onClick(flavorTag);
    }
  };

  return (
    <div 
      ref={tagRef}
      className={getTagClasses()} 
      onClick={handleClick}
      onMouseEnter={() => setTooltipVisible(true)}
      onMouseLeave={() => setTooltipVisible(false)}
    >
      {flavorTag?.tag.name}
      
      {showTooltip && tooltipVisible && (
        <div className="tooltip">
          <div className="tooltip-content">
            {getTooltipContent()}
          </div>
        </div>
      )}
    </div>
  );
};

export default FlavorTagPill;