import { EffectData, Tag } from "../../models";
import CImage from "./cimage";

interface EffectDisplayProps {
	effectData: EffectData;
}

const EffectDisplay: React.FC<EffectDisplayProps> = ({ effectData }) => {
	return (
		<div className="effect-display">
			<div className="effect-display-header">
				<div className="effect-display-name">{effectData.object === 'party' ? 'Party' : effectData.object.name}</div>
				{effectData.object !== 'party' && effectData.object.icon && <CImage imageId={effectData.object.icon} />}
			</div>

			{effectData.effects?.map((effect, idx) => (
				<div className={`effect-display-effect ${effect.class}`} key={idx}>
					{effect.effect}
				</div>
			))}
		</div>
	);
};

export default EffectDisplay;
