/**
 * Utility functions for playing sounds in the application
 */

// Sound effects for the slot machine
const SLOT_MACHINE_SOUNDS = {
  SPIN: 'https://assets.mixkit.co/active_storage/sfx/2016/2016-preview.mp3',
  WIN_SMALL: 'https://assets.mixkit.co/active_storage/sfx/270/270-preview.mp3',
  WIN_MEDIUM: 'https://assets.mixkit.co/active_storage/sfx/2000/2000-preview.mp3',
  WIN_BIG: 'https://assets.mixkit.co/active_storage/sfx/2017/2017-preview.mp3',
  REEL_STOP: 'https://assets.mixkit.co/active_storage/sfx/178/178-preview.mp3',
  BUTTON_CLICK: 'https://assets.mixkit.co/active_storage/sfx/1705/1705-preview.mp3',
  WILD_COLLECT: 'https://assets.mixkit.co/active_storage/sfx/150/150-preview.mp3', // Magical sound for wild collection
  WILD_STORM_TRIGGER: 'https://assets.mixkit.co/sfx/preview/mixkit-cinematic-whoosh-explosion-1022.mp3', // Example sound
  WILD_APPLIED: 'https://assets.mixkit.co/sfx/preview/mixkit-fast-magic-game-spell-883.mp3',         // Example sound
};

// Cache for audio objects to prevent recreating them each time
const audioCache: { [key: string]: HTMLAudioElement } = {};

/**
 * Preload sounds into cache
 */
export const preloadSounds = (): void => {
  Object.values(SLOT_MACHINE_SOUNDS).forEach(url => {
    if (!audioCache[url]) {
      const audio = new Audio(url);
      audio.load();
      audioCache[url] = audio;
    }
  });
};

/**
 * Play a sound effect
 * @param url Sound URL to play
 * @param volume Volume level (0-1)
 */
const noSound = true;
export const playSound = (url: string, volume = 0.5): void => {
  
  
  
  if(noSound) {
    return;
  }
  
  try {
    let audio: HTMLAudioElement;
    
    // Use cached audio if available
    if (audioCache[url]) {
      audio = audioCache[url];
    } else {
      audio = new Audio(url);
      audioCache[url] = audio;
    }
    
    // Reset the audio to the beginning if it's already playing
    audio.currentTime = 0;
    audio.volume = volume;
    
    // Play the sound
    audio.play().catch(error => {
      console.error('Error playing sound:', error);
    });
  } catch (error) {
    console.error('Error playing sound:', error);
  }
};

export { SLOT_MACHINE_SOUNDS };
