
interface CboxProps {
  title: string;
  theme?: string;
  children?: React.ReactNode;
}
const Cbox: React.FC<CboxProps> = ({ title, theme, children }) => {
  return (
    <div className={`cbox ${theme}`}>
      <div className={`cbox-text ${theme}`}>
        {title}
      </div>
      <div className="cbox-children">
        {children}
      </div>
    </div>
  );
};

export default Cbox;