import { Tag, ThemeConfig, TRAVEL_OPTION, TravelOption } from "../../models";
import { useCampaignManager } from "../managerContexts";
import TooltipWrapper from "./TooltipWrapper";

interface TravelOptionProps {
    travelOption: TravelOption;
    theme?: ThemeConfig;
    onClick: () => void
    length?: string;
}

const TravelOptionDisplay: React.FC<TravelOptionProps> = ({ travelOption, theme, onClick, length }) => {
    const { id, name, description, time, tags, score, boredom, tag, exhaustion, travelOptionType } = travelOption;
    const campaignManager = useCampaignManager();

    const eventTag = campaignManager.getEventTag(travelOption);

    const hasTravelTag = (tag: Tag) => {
      //  return true
        return campaignManager.hasTravelTag(tag);
    }

    const displayValue = (value: number | undefined) => {
        if (value === -1) return '?';
        return value;
    };
    // Determine if the option is locked
    const isLocked = eventTag && !hasTravelTag(eventTag);


    // Determine the tooltip copy based on the travelOptionType
    const getTagDisplayTooltipContent = (travelOptionType: TRAVEL_OPTION | undefined) => {
        switch (travelOptionType) {
            case TRAVEL_OPTION.EXPLORE_UPCOMING_LOCATION:
                return 'Explore the upcoming location';
            case TRAVEL_OPTION.CAMP:
                return 'Camp for the night';
            case TRAVEL_OPTION.AMBUSH:
                return 'Ambush while camping';
            case TRAVEL_OPTION.TRADERS:
                return 'Meet traders on the road';
            case TRAVEL_OPTION.SIDE_QUEST:
                return 'Discover a hidden cache';
            default:
                return '';
        }
    };

    
    return (
        <div className={`travel-option ${length} ${isLocked ? 'locked' : 'unlocked'}`} onClick={onClick}>
             {isLocked && (
                <div className="locked-banner">
                    <span>UNDISCOVERED</span>
                </div>
            )}
            <div className="travel-option-header">
                <span className="travel-option-name">{name}</span>
                {eventTag && <span className={`travel-option-tag text-pill type-travel ${hasTravelTag(eventTag)? '' : 'missing'}`}>{eventTag.name}</span>}
                <div className="travel-option-description">{description}</div>
                 </div>
            {!isLocked ? (
                <>
                    <TooltipWrapper content="Journey Length increase" preservePositioning={true}>
                        <span className="travel-option-detail">🕒:{displayValue(time)}</span>
                    </TooltipWrapper>
                    <TooltipWrapper content="Boredom Risk Increase" preservePositioning={true}>
                        <span className="travel-option-detail time">👁️:{displayValue(boredom)}</span>
                    </TooltipWrapper>
                    <TooltipWrapper content={getTagDisplayTooltipContent(travelOptionType)} preservePositioning={true}>
                        <span className="travel-option-detail tags text-pill type-location">
                            {tags}
                        </span>
                    </TooltipWrapper>
                </>
            ) : (
                <>
                    <span className="travel-option-detail">🕒:{displayValue(time)}</span>
                    <span className="travel-option-detail time">👁️:{displayValue(boredom)}</span>
                    <span className="travel-option-detail tags text-pill type-location">
                        {tags}
                    </span>
                </>
            )}            {score !== undefined && score !== 0 && (
                isLocked ? (
                    <span className="travel-option-detail score">Score: {displayValue(score)}</span>
                ) : (
                    <TooltipWrapper content="Impact on campaign score" preservePositioning={true}>
                        <span className="travel-option-detail score">Score: {displayValue(score)}</span>
                    </TooltipWrapper>
                )
            )}
            {exhaustion !== undefined && exhaustion !== 0 && (
                isLocked ? (
                    <span className="travel-option-detail score">Exhaustion: {displayValue(exhaustion)}</span>
                ) : (
                    <TooltipWrapper content="Exhaustion affects character performance" preservePositioning={true}>
                        <span className="travel-option-detail score">Exhaustion: {displayValue(exhaustion)}</span>
                    </TooltipWrapper>
                )
            )}
            
        </div>
    );
};

export default TravelOptionDisplay;