export class Graph {
  private nodes: Map<string, any>;
  private edges: Map<string, Map<string, number>>;

  constructor() {
    this.nodes = new Map();
    this.edges = new Map();
  }

  addNode(node: any) {
    this.nodes.set(node.name, node);
    this.edges.set(node.name, new Map());
  }

  addEdge(from: string, to: string, weight: number) {
    this.edges.get(from)?.set(to, weight);
  }

  getNode(name: string) {
    return this.nodes.get(name);
  }
}

