import React from "react";
import "./notebook.scss";
import { EncounterNotebookEntry } from "../../models";
import CImage from "./cimage";
import { useCampaignManager } from "../managerContexts";
import Cbox from "./cbox";

interface NoteBookEncounterProps {
  encounterInfo: EncounterNotebookEntry;
}

const NotebookEncounter: React.FC<NoteBookEncounterProps> = ({
  encounterInfo,
}) => {
  const campaignManager = useCampaignManager();
  const encounter = encounterInfo.encounter;

  const combinedTags = campaignManager.getTagsForEncounter(encounter);
  const encounterBranches = encounterInfo.encounter.branches;

  return (
    <div className="notebook-encounter-container">
      <div className="notebook-encounter-contents left">
        <Cbox title={encounter.name} theme="encounter-info">
          <div className="notebook-icon-container">
            <CImage imageId={encounter.icon} />
          </div>
        </Cbox>
       
      </div>
      <Cbox title="DETAILS" theme="details">
        <Cbox title="LOCATION" theme="location">
          <div className="encounter-location">{encounter.location.name}</div>
        </Cbox>
        <Cbox title="DESCRIPTION" theme="description">
          {" "}
          <div className="notebook-text encounter-description">
            {encounter.description}
          </div>
        </Cbox>
        <Cbox title="ELEMENTS" theme="elements">
          <div className="elements-features-list-container">
            {combinedTags.map((tag, index) => {
              return (
                <div
                  key={index}
                  className="notebook-text elements-features-list text-pill type-encounter"
                >
                  {tag.name}{" "}
                </div>
              );
            })}
          </div>
        </Cbox>
      </Cbox>
    </div>
  );
};

export default NotebookEncounter;
