import EventEmitter from 'events';
import { Character, HeroCombatant, Player, Tag } from '../models';
import AppManager from './appManager';
import CampaignManager from './campaignManager';
import CharacterManager from './characterManager';
import DataManager from './dataManager';
import PlayerManager from './playerManager';
import UiManager from './uiManager';

class PartyManager extends EventEmitter {

  private dataManager: DataManager | undefined;
  private appManager: AppManager | undefined;
  private characterManager: CharacterManager | undefined;
  private campaignManager: CampaignManager | undefined;
  private playerManager: PlayerManager | undefined;
  private uiManager: UiManager | undefined;
  private partyScore: number = 0;
  private partyTension: number = 0;
  private treasureLevel: number = 0;
  private treasurePoints: number = 0;
  partyExhaustion: number = 0;

  
  private stressColors = ['#FFB347', '#FFDF80', '#FFDF80', '#B2FF66', '#B2FF66', '#B2FF66', '#66FF66', '#FF7043', '#FF7043', '#FF6666'];
  constructor(appManager: AppManager) {
    super();
    setTimeout(() => {
      this.appManager = appManager;
      this.dataManager = appManager.getDataManager();
      this.characterManager = appManager.getCharacterManager();
      this.campaignManager = appManager.getCampaignManager();
      this.playerManager = appManager.getPlayerManager();
      this.uiManager = appManager.getUiManager();
    }, 0);

  }

  perceptionEventReveal(character: Character) {
    // We need to bump the player's engagement, and decreae all other players engamgement
    const players = this.playerManager?.getPlayers();
    players?.forEach((player) => {
      if(player.characters.includes(character)) {
        player.playerStats.engagement = Math.min(player.playerStats.maxEngagement, Math.max(player.playerStats.engagement + 1, 0));
      } else {
        player.playerStats.engagement = Math.min(player.playerStats.maxEngagement, Math.max(player.playerStats.engagement - 1, 0));

      }
    });
    this.uiManager?.updateParty();
  }

 
  getPartyStrength() {
    const players = this.playerManager?.getPlayers();
    // sum the levels for now
    let strength = 0;
    players?.forEach((player) => {
      player.characters.forEach((character) => {
        if(character) {
          strength += character.level;
        }
      });
    });
    return strength;
  }

  playersTakeAction(playersAttended?: string[], reduceAllOthers = true, addAmount = 1) {
    const players = this.playerManager?.getPlayers();
    players?.forEach((player) => {
      if(playersAttended?.includes(player.name)) {
        player.playerStats.involvement = Math.min(player.playerStats.involvement + addAmount, player.playerStats.maxInvolvement );
        this.playerManager?.addPlayerExp(player, addAmount);
      }
      else if(reduceAllOthers) {
       this.updateAttention(player, -addAmount);
      }
    });
    this.uiManager?.updateParty();
  }

  updateAllAttention(amount: number) {
    const players = this.playerManager?.getPlayers();
    players?.forEach((player) => {

      this.updateAttention(player, amount);
    });
    this.uiManager?.updateParty();
  }

  updateAttention(player: Player, amount: number) {
    if(player) {
      if(amount < 0 && player.playerStats.involvement === 0) {
        player.playerStats.enjoyment = Math.max(player.playerStats.enjoyment - 1, 0);
      }
      player.playerStats.involvement = Math.max(0, Math.min(player.playerStats.involvement + amount, player.playerStats.maxInvolvement));
    }
    this.uiManager?.updateParty();
  }

  multiplyPartyTension(amount: number) {
    // const players = this.playerManager?.getPlayers();
    // players?.forEach((player) => {
    //   player.playerStats.stress *= amount;
    // });
    this.partyTension *= amount;
    this.uiManager?.updateParty();
  }

  addPartyTension(amount: number) { 
    // const players = this.playerManager?.getPlayers();
    // players?.forEach((player) => {
    //   player.playerStats.stress += amount;
    // });

    this.partyTension += amount;
    if(this.partyTension > 100) {
      this.partyTension = 100;
    }
    this.uiManager?.updateParty();
  }

  setPartyTension(amount: number) {
    // const players = this.playerManager?.getPlayers();
    // players?.forEach((player) => {
    //   player.playerStats.stress = amount;
    // });
    this.partyTension = amount;
    this.uiManager?.updateParty();
  }

  takeBreak() {
    this.partyTension = this.partyTension * 0.5;
    // note, this will not affect the mood. 
    this.updateAllAttention(-2);
    this.partyExhaustion = this.partyExhaustion * 0.5;
    this.emit('dmScreenUpdated', this.partyExhaustion);
    this.uiManager?.updateParty();
  }

  getPartyExhaustion() {
    return this.partyExhaustion;
  }
  
  exhaustParty(amount:  number) {
    this.partyExhaustion += amount;
    this.emit('dmScreenUpdated', this.partyExhaustion);
  }
  addPartyEngagement(amount: number) {  
    const players = this.playerManager?.getPlayers();
    players?.forEach((player) => {
      player.playerStats.engagement = Math.min(player.playerStats.maxEngagement, Math.max(player.playerStats.engagement + amount, 0));
    });
    this.uiManager?.updateParty();
  }

  addPartyScore(amount: number) {
    this.partyScore += amount * this.getTensionScoreMultiplier();
    this.uiManager?.updateParty();
  }

  getPartyScore() {
    return this.partyScore;
  }


  addInjury(character: Character): string {
    if(!character) { 
      return ' invalid';
    }
    character.injury = character.injury === 'injured' ? 'dead' : 'injured';


    const player = this.playerManager?.getPlayerAssignedCharacter(character);
    if(player){
      if(character.injury === 'injured') {
        player.playerStats.enjoyment = Math.max(player.playerStats.enjoyment - 1, 0);
      }else{
        player.playerStats.enjoyment = Math.max(player.playerStats.enjoyment - 1, 0);
      }
    }


    this.uiManager?.updateParty();
    return character.injury;
  }

  addEnjoyment(player: Player, amount: number) {
    if(player) {
      player.playerStats.enjoyment = Math.min(player.playerStats.maxEnjoyment, Math.max(player.playerStats.enjoyment + amount, 0));
      this.uiManager?.updateParty();
    }
  }

  getTreasureLevel() { 
    return this.treasureLevel;
  }

  getTreasurePoints() {
    return this.treasurePoints;
  }

  getTreasureThreshold(level: number): number {
    return level + 1;
  };
  

  addTreasurePoints(points: number) {
    this.treasurePoints += points;
    while (this.getTreasureThreshold(this.treasureLevel + 1) <= this.treasurePoints) {
      this.treasureLevelUp(this.treasureLevel);
      this.treasureLevel++;
      this.treasurePoints = 0;
      
    }
    this.uiManager?.updateParty();
  }


  treasureLevelUp(level: number) {
    const reward = this.getTreasureReward(level);
    if(reward.type === 'score') {
      this.addPartyScore(reward.value);
    } else {
      const players = this.playerManager?.getPlayers();
      players?.forEach((player) => {
        player.playerStats.enjoyment = Math.min(player.playerStats.maxEnjoyment, player.playerStats.enjoyment + reward.value);
      });
    }
  }

  getTreasureReward = (level: number) => {
    if(level % 2 === 0) {
      return { type: 'attitude', value: 1 };
    }
    return { type: 'score', value: 5 * (level+1) };
  };

  addPartyEnjoyment(amount: number) {
    const players = this.playerManager?.getPlayers();
    players?.forEach((player) => {
      player.playerStats.enjoyment = Math.min(player.playerStats.maxEnjoyment, Math.max(player.playerStats.enjoyment + amount, 0));
    });
    this.uiManager?.updateParty();
  }

  replenishTools(character: Character, amount?: number) {
    if(!character.attributeLoss || !character.attributeLoss.tools) {
      return;
    }

    if(!amount) {
      amount = character.attributeLoss.tools;
    }

    character.attributeLoss.tools = Math.max(0, character.attributeLoss.tools - amount);
    character.attributeLoss.tools = 0;
  } 

  replenishPartyTools() {
    const players = this.playerManager?.getPlayers();
    players?.forEach((player) => {
      player.characters.forEach((character) => {
        if(character && character.attributeLoss) {
          character.attributeLoss.tools = 0;
        }
      });
    });
    this.uiManager?.updateParty();
  }


  healRandomCharacter() {
    const players = this.playerManager?.getPlayers();
    const injuredCharacters: Character[] = [];
    players?.forEach((player) => {
      player.characters.forEach((character) => {
        if(character && character.injury) {
          injuredCharacters.push(character);
        }
      });
    });
    if(injuredCharacters.length === 0) {
      return;
    }
    const randomIndex = Math.floor(Math.random() * injuredCharacters.length);
    const character = injuredCharacters[randomIndex];
    character.injury = undefined;
  }

  healParty() {
    const players = this.playerManager?.getPlayers();
    players?.forEach((player) => {
      player.characters.forEach((character) => {
        if(character) {
          character.injury = undefined;
        }
      });
    });
    this.uiManager?.updateParty();
  }

  healCharacter(character: Character) {
    if(!character) {
      return;
    }
    character.injury = undefined;
    this.uiManager?.updateParty();
  }

  raiseRandomFromDead() {
    const players = this.playerManager?.getPlayers();
    const deadCharacters: Character[] = [];
    players?.forEach((player) => {
      player.characters.forEach((character) => {
        if(character && character.injury === 'dead') {
          deadCharacters.push(character);
        }
      });
    });
    if(deadCharacters.length === 0) {
      return;
    }
    const randomIndex = Math.floor(Math.random() * deadCharacters.length);
    const character = deadCharacters[randomIndex];
    character.injury = undefined;
  }

  replenishPartyMagic() {
    const players = this.playerManager?.getPlayers();
    players?.forEach((player) => {
      player.characters.forEach((character) => {
        if(character && character.attributeLoss) {
          character.attributeLoss.magic = 0;
        }
      });
    });
    this.uiManager?.updateParty();
  }
  replenishMagic(character: Character, amount?: number) {
    if(!character.attributeLoss 
      || !character.attributeLoss.magic
    ) {
      return;
    }

    if(!amount) {
      amount = character.attributeLoss.magic;
    }
    character.attributeLoss.magic = Math.max(0, character.attributeLoss.magic - amount);

    this.uiManager?.updateParty();
  }

  levelUpLowestCharacter() {
    const players = this.playerManager?.getPlayers();
    let lowestLevel = 100;
    let lowestCharacter: Character | undefined;
    players?.forEach((player) => {
      player.characters.forEach((character) => {
        if(character && character.level < lowestLevel) {
          lowestLevel = character.level;
          lowestCharacter = character;
        }
      });
    });
    if(lowestCharacter) {
      lowestCharacter.level += 1;
    }
  }

  levelUp(character: Character) {
    if(character) {
      character.level += 1;
    }
  }

  //stress action
  //any action which will cause stress to increase when tension is high
  stressAction(affectedPlayers? : Player[]) {
    const players = this.playerManager?.getPlayers();
    const stressIncrease = this.getStressIncrease(this.partyTension) || 0;
    players?.forEach((player) => {
      if(!affectedPlayers || affectedPlayers.includes(player)) {
       player.playerStats.stress = Math.min(100, player.playerStats.stress + stressIncrease);
      }
    });
    this.uiManager?.updateParty();
  }



    // Make a unified wrapper for both hero and NPCs for the display. 
   generateHeroes(npcs: Tag[]) {

      const heroCombatants: HeroCombatant[] = [];
      npcs.forEach((npc) => {
        const heroCombatant: HeroCombatant = {
          npcTag: npc,
          icon: `combat_${npc.name}`,
          name: npc.name,
        };
        heroCombatants.push(heroCombatant);
      });
     
      const characters = this.characterManager?.getPlayerCharacters();
      characters?.forEach((character) => {
        const heroCombatant: HeroCombatant = {
          character: character,
          icon: character.icon,
          name: character.name,
        };
        heroCombatants.push(heroCombatant);
      });


      return heroCombatants;
    }

    getPartyTension() {
     return this.partyTension;
    }

    private tensionDescriptions = [
      { max: 10, description: "Too Low", scoreMultiplier: 0, stressIncrease: 0 },
      { max: 30, description: "Very Low", scoreMultiplier: 0.5, stressIncrease: 0 },
      { max: 60, description: "Good", scoreMultiplier: 1, stressIncrease: 0 },
      { max: 70, description: "Perfect!", scoreMultiplier: 2, stressIncrease: 0 },
      { max: 90, description: "Stress", scoreMultiplier: 1, stressIncrease: 5 },
      { max: 100, description: "High Stress", scoreMultiplier: 1, stressIncrease: 10 },
      { max: Infinity, description: "Impossible", scoreMultiplier: 0, stressIncrease: 20 }
    ];

    getTensionScoreMultiplier() {
      for (const tension of this.tensionDescriptions) {
        if (this.partyTension <= tension.max) {
          return tension.scoreMultiplier;
        }
      }
      return 0;
    }

    getTensionDescription(value: number) {
      for (const tension of this.tensionDescriptions) {
        if (value <= tension.max) {
          return tension.description;
        }
      }
      return "Unknown Tension";
    }

    getTensionDescription2(value: number) {
      let mult = 0;
      for (const tension of this.tensionDescriptions) {
        if (value <= tension.max) {
          mult = tension.scoreMultiplier;
          break;
        }
      }

      if(mult === 0) {
        return "none";
      }if(mult === 0.5) {
        return "1/2";
      }if(mult === 1) {
        return "normal";
      }
      return `x${mult}`;
    }

    getScoreMultiplier(value: number) {
      for (const tension of this.tensionDescriptions) {
      if (value <= tension.max) {
        return tension.scoreMultiplier;
      }
      }
    }

    getStressIncrease(value: number) {
      for (const tension of this.tensionDescriptions) {
        if (value <= tension.max) {
          return tension.stressIncrease;
        }
      }
      return 0;
    }

    getStressColors() {
      return this.stressColors;
    }

}

export default PartyManager;